<template>
  <v-form
    ref="form"
    v-model="valid"
    lazy-validation
    class="px-12 py-3 col-md-12"
  >
    <base-loader :show="showLoader" />
    <v-col cols="12">
      <v-autocomplete
        label="Cliente"
        :items="['Unimed', 'Breitaupt', 'Cassol']"
      />
    </v-col>

    <v-btn
      color="primary"
      title="Cadastrar Novo Projeto"
      small
      @click="openNewProject"
    >
      Adicionar projeto
    </v-btn>
    <v-expansion-panels class="project-table">
      <v-expansion-panel
        v-for="(prj, i) in projetos"
        :key="i"
      >
        <v-expansion-panel-header>
          Projeto para produção de {{ produtos[prj.produto] }} com {{ prj.parametros.quantidadeBancadas }} bancada(s) e {{ prj.parametros.quantidadeLinhas }} linha(s)
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-btn
            class="mr-2 warning"
            @click="deleteProject"
          >
            Remover projeto do orçamento
          </v-btn>
          <v-simple-table>
            <thead>
              <tr>
                <th>Posição</th>
                <th>Produto</th>
                <th>Quantidade</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, chave) in prj.itens"
                :key="chave"
              >
                <td> {{ chave + 1 }} </td>
                <td> {{ item.descricao }} </td>
                <td>
                  <v-text-field
                    v-model="item.quantidade"
                    style="width:30px !important"
                  />
                </td>
              </tr>
            </tbody>
          </v-simple-table>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <v-spacer />
    <v-btn
      color="primary"
      class="mr-4"
      @click="persist"
    >
      Salvar persist
    </v-btn>

    <v-btn
      color="warning"
      @click="returnPage"
    >
      Cancelar
    </v-btn>
    <v-dialog
      v-model="showModal"
      persistent
      max-width="900px"
    >
      <v-card>
        <v-card-title>
          <span class="headline">Projeto</span>
        </v-card-title>
        <v-card-text>
          <v-tabs>
            <v-tab>
              Pedido
            </v-tab>
            <v-tab>
              Configuração
            </v-tab>
            <v-tab-item>
              <v-container>
                <v-row v-if="projeto.formPage == 1">
                  <v-col cols="4">
                    <v-select
                      v-model="projeto.perfil"
                      label="Perfil"
                      :items="projeto.perfis"
                      item-text="descricao"
                      item-value="neoId"
                      required
                      @change="loadProducts"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-select
                      v-model="projeto.produto"
                      label="Produto"
                      :item-text="getDescription"
                      :items="projeto.produtos"
                      item-value="neoId"
                      required
                      @change="getPassos"
                    />
                  </v-col>
                  <v-col cols="2">
                    <v-text-field
                      v-model="projeto.passos"
                      label="Passo"
                      disabled="1"
                    />
                  </v-col>
                  <v-col cols="2">
                    <v-text-field
                      v-model="projeto.furos"
                      label="Dist Furos"
                      disabled="1"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-autocomplete
                      v-model="projeto.injetor"
                      label="Injetores"
                      :item-text="getDescription"
                      :items="projeto.injetores"
                      item-value="neoId"
                      required
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.parametros.tamanhoDaEstufa"
                      label="Tamanho Estufa"
                      required
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.parametros.largura"
                      label="Largura"
                      type="number"
                      required
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.parametros.comprimento"
                      label="Comprimento"
                      type="number"
                      required
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.barras"
                      label="Barras"
                      type="text"
                      placeholder="Dividir com traço o tamanho de cada barra"
                      title="Dividir com traço o tamanho de cada barra"
                      required
                      @keyup="calcTotalBar"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.totalBar"
                      label="Tamanho total"
                      type="number"
                      disabled="1"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.parametros.quantidadeLinhas"
                      label="Quantidade de Linhas"
                      type="number"
                      required
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.parametros.quantidadeBancadas"
                      label="Quantidade de Bancadas"
                      type="number"
                      required
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-autocomplete
                      v-model="projeto.saidaColetor"
                      label="Saida Coletor"
                      :items="['','C/ Saida Esq', 'C/ Saida Dir']"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-checkbox
                      v-model="projeto.containsTorreHidroponia"
                      label="Incluir torre de hidroponia?"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-autocomplete
                      v-if="projeto.containsTorreHidroponia"
                      v-model="projeto.voltagemTorre"
                      label="Voltagem da Torre"
                      :items="['220v', '110v']"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-if="projeto.containsTorreHidroponia"
                      v-model="projeto.quantidadeTorre"
                      type="number"
                      label="Quantidade de torres"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-checkbox
                      v-model="projeto.containsMesas"
                      label="Possui Mesas?"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-select
                      v-if="projeto.containsMesas"
                      v-model="projeto.mesa"
                      label="Mesa"
                      :items="projeto.mesasDeGerminacao"
                      :item-text="getMesaDescription"
                      item-value="neoId"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-if="projeto.containsMesas"
                      v-model="projeto.qtdMesa"
                      label="Quantidade de mesas"
                      type="number"
                    />
                  </v-col>
                  <v-col cols="12">
                    <v-checkbox
                      v-model="projeto.containsCavaletes"
                      label="Possui cavaletes?"
                    />
                  </v-col>
                </v-row>
                <v-row v-else>
                  <v-col cols="12">
                    <v-simple-table style="background: none">
                      <thead>
                        <tr>
                          <th>
                            <h2>#</h2>
                          </th>
                          <th>
                            <h2>Quantidade</h2>
                          </th>
                          <th>
                            <h2>Unidade</h2>
                          </th>
                          <th>
                            <h2>Descrição</h2>
                          </th>
                          <th>
                            <h2>TEC</h2>
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          v-for="(item, i) in projeto.itens"
                          :key="i"
                        >
                          <td> {{ i+1 }}</td>
                          <td> {{ item.quantidade }}</td>
                          <td> Pç</td>
                          <td>{{ item.descricao }}</td>
                          <td>{{ item.ferramenta }}</td>
                        </tr>
                      </tbody>
                    </v-simple-table>
                  </v-col>
                </v-row>
              </v-container>
            </v-tab-item>
            <v-tab-item>
              <v-container v-if="projeto.config">
                <v-row>
                  <v-col cols="6">
                    <v-text-field
                      v-model="projeto.config.distanciaMaxCavaletes"
                      label="Distância máxima entre os cavaletes (sugerido)"
                    />
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      v-model="projeto.config.distanciaEntCavalete"
                      label="Distância da emenda até o cavalete (valor fixo)"
                    />
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      v-model="projeto.config.distanciaInicioPerfilCavalete"
                      label="Distância do início do perfil até o primeiro cavalete"
                    />
                  </v-col>
                  <v-col cols="6">
                    <v-text-field
                      v-model="projeto.config.distanciaUltCavalete"
                      label="Distância do último cavalete até o final do perfil Hortivinyl"
                    />
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model="projeto.config.distanciaUltCavalete"
                      label="Distância do último cavalete até o final do perfil Hortivinyl"
                    />
                  </v-col>
                </v-row>
              </v-container>
            </v-tab-item>
          </v-tabs>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            color="blue darken-1"
            text
            @click="showModal = false"
          >
            Cancelar
          </v-btn>
          <v-btn
            v-if="projeto.formPage == 2"
            color="blue darken-1"
            text
            @click="projeto.formPage = 1"
          >
            Alterar Especificações
          </v-btn>
          <v-btn
            v-if="projeto.formPage == 1"
            color="blue darken-1"
            text
            @click="viewProjectItens"
          >
            Ver itens
          </v-btn>
          <v-btn
            v-else
            class="primary"
            text
            @click="addOnList"
          >
            Salvar adicionarLista projetos
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-form>
</template>
<script>

  import axios from 'axios'

  export default {
    name: 'CarrierForm',
    components: {
    },
    data: function () {
      return {
        showModal: false,
        showLoader: false,
        valid: true,
        produtos: [],
        urlbase: 'http://fusionhomolog:8080/fusion/custom/',
        projeto: {},
        projetoModelo: {
          itens: [],
          qtdMesa: null,
          containsMesas: false,
          variaveis: {
            cavaletes: {
              total: null,
              travessas: null,
              calunas: null,
              distancia: null,
            },
            conexoes: {
              tampaEntrada: null,
              tampaSaida: null,
              emenda: null,
            },
            coletores: {
              quantidade: null,
              tamanho: null,
              quantidadeTampas: null,
            },
            engenharia: {
              compriMentoConsiderado: null,
              quantidadeCavaletes: null,
              vaoCavaletes: null,
              cavaletesInternos: null,
            },
          },
          parametros: {
            comprimento: null,
            tamanhoDaEstufa: null,
            largura: null,
            quantidadeBancadas: null,
            quantidadeLinhas: null,
          },
          containsTorreHidroponia: false,
          quantidadeTorre: null,
          voltagemTorre: null,
          formPage: 1,
          barras: null,
          totalBar: null,
          perfil: {},
          config: {},
          perfis: [],
          ferramenta: {},
          furos: '',
          mesas: [],
          produto: {
            descricao: 'Selecione um perfil',
          },
          produtos: [],
          injetor: {},
          injetores: [],
          mesasDeGerminacao: [],
        },
        projetos: [],
      }
    },
    created () {
      axios.get(`${this.urlbase}customApi.jsp?entity=hidProd`)
        .then(({ data }) => {
          data.forEach((item) => {
            this.produtos[item.neoId] = item.descricao
          })
        }, (error) => {
          this.writeLog(error)
          this.loginError = true
        })
    },
    methods: {
      getSecureFloat (dirtyFloat) {
        if (dirtyFloat.indexOf(',') > 0) {
          dirtyFloat = dirtyFloat.replace(',', '.')
        }

        return parseFloat(dirtyFloat)
      },
      getMesaDescription (param) {
        const tamanho = parseFloat(param.tamanho).toFixed(2)

        return `${param.descricao} ${tamanho}M`
      },
      openNewProject () {
        const template = JSON.stringify(this.projetoModelo)
        this.projeto = JSON.parse(template)
        this.showLoader = true
        this.projeto.formPage = 1
        this.loadItens()
        this.showLoader = false
        this.showModal = true
      },
      addOnList () {
        this.projetos.push(this.projeto)
        this.showModal = false
        this.projeto = {}
      },
      loadItens () {
        this.loadPerfis()
        this.loadInjetores()
        this.loadMesas()
        this.loadConfig()
      },
      loadConfig () {
        axios.get(`${this.urlbase}customApi.jsp?entity=hidEng`)
          .then(({ data }) => {
            data.forEach(item => {
              this.projeto.config = item
            })
          }, (error) => {
            this.writeLog(error)
            this.loginError = true
          })
      },
      loadMesas () {
        axios.get(`${this.urlbase}customApi.jsp?entity=megehid`)
          .then(({ data }) => {
            data.forEach(item => {
              this.projeto.mesasDeGerminacao.push(item)
            })
          }, (error) => {
            this.writeLog(error)
            this.loginError = true
          })
      },
      loadInjetores () {
        axios.get(`${this.urlbase}customApi.jsp?entity=hidinj`)
          .then(({ data }) => {
            data.forEach(item => {
              item.descricao = `${item.descricao} ${item.dimensao}`
              this.projeto.injetores.push(item)
            })
          }, (error) => {
            this.writeLog(error)
            this.loginError = true
          })
      },
      loadPerfis () {
        axios.get(`${this.urlbase}customApi.jsp?entity=hidPrf`)
          .then(({ data }) => {
            data.forEach(item => {
              this.projeto.perfis.push(item)
            })
          }, (error) => {
            this.writeLog(error)
            this.loginError = true
          })
      },
      getPerfil () {
        return this.projeto
          .perfis
          .find((perfil) => perfil.neoId === this.projeto.perfil)
      },
      loadProducts () {
        const perfil = this.getPerfil()

        this.projeto.produtos = perfil.produtos
        this.projeto.ferramenta = perfil.ferramenta
        this.projeto.produto = {}
      },
      getPassos () {
        this.projeto.passos = this
          .projeto
          .produtos
          .find((produto) => produto.neoId === this.projeto.produto)
          .passo

        this.projeto.furos = this
          .projeto
          .produtos
          .find((produto) => produto.neoId === this.projeto.produto)
          .distfuros
      },
      returnPage () {
        this.$router.go(-1)
      },
      persist () {
        this.showModal = false
        axios.post(`${this.urlbase}persistApi.jsp`, this.projeto)
          .then(({ data }) => {
            this.$router.push({ name: 'Portal da Hidroponia' })
          }, (error) => {
            this.writeLog(error)
            this.loginError = true
          })
      },
      addItemProjeto (quantidade, descricao, ferramenta) {
        try {
          if (quantidade && parseInt(quantidade) > 0) {
            const item = {
              quantidade,
              descricao,
            }

            this.projeto.itens.push(item)
          }
        } catch {
          return null
        }
      },
      calcTotalBar () {
        const barras = this.projeto.barras.split('-')
        this.projeto.totalBar = barras.reduce((sum, x) => parseFloat(sum) + parseFloat(x))
      },
      calcularVariáveis () {
        this.projeto.variaveis.coletores.quantidade = 1 * this.projeto.parametros.quantidadeBancadas
        this.projeto.variaveis.coletores.tamanho = (parseFloat(this.projeto.parametros.largura) + 0.07).toFixed(2) // + 100.0001
        this.projeto.variaveis.coletores.quantidadeTampas = 2
        const calcEmendas = () => {
          const emendas = (this.projeto.barras.split('-').length - 1) * this.projeto.parametros.quantidadeLinhas
          return emendas * this.projeto.parametros.quantidadeBancadas
        }
        this.projeto.variaveis.conexoes.tampaEntrada = this.projeto.parametros.quantidadeLinhas * this.projeto.parametros.quantidadeBancadas
        this.projeto.variaveis.conexoes.tampaSaida = this.projeto.parametros.quantidadeLinhas * this.projeto.parametros.quantidadeBancadas
        this.projeto.variaveis.conexoes.emenda = calcEmendas()

        if (this.projeto.containsCavaletes) {
          const calcTotalCavaletes = () => {
            const comprimentoUtil = parseFloat(this.projeto.parametros.comprimento) -
              (parseFloat(this.projeto.config.distanciaInicioPerfilCavalete) + parseFloat(this.projeto.config.distanciaUltCavalete))
            const totalCavalete = Math.floor(comprimentoUtil / this.projeto.config.distanciaMaxCavaletes)
            return totalCavalete + 2
          }

          this.projeto.variaveis.cavaletes.total = calcTotalCavaletes()
          this.projeto.variaveis.cavaletes.travessas = this.projeto.variaveis.cavaletes.total
        }
      },
      addTorre () {
        if (this.projeto.containsTorreHidroponia) {
          const item = {}
          item.quantidade = this.projeto.quantidadeTorre
          item.descricao = `Torre de Cultivo Hortivinyl ${this.projeto.voltagemTorre}`
          this.projeto.itens.push(item)
        }
      },
      adicionarTampasPedido () {
        if (this.projeto.variaveis.conexoes.tampaEntrada) {
          this.addItemProjeto(this.projeto.variaveis.conexoes.tampaEntrada,
                              `Tampa de entrada para Perfil Hortivinyl PVC ${this.getPerfil().descricao}mm TEC${this.projeto.ferramenta.descricao}`,
                              this.projeto.ferramenta.descricao,
          )
        }
        if (this.projeto.variaveis.conexoes.tampaSaida) {
          const item = {}
          item.quantidade = this.projeto.variaveis.conexoes.tampaSaida
          item.descricao = `Tampa de saída para Perfil Hortivinyl PVC ${this.getPerfil().descricao}mm TEC${this.projeto.ferramenta.descricao}`
          item.ferramenta = this.projeto.ferramenta.descricao

          this.projeto.itens.push(item)
        }
        if (this.projeto.variaveis.conexoes.emenda) {
          const item = {}
          item.quantidade = this.projeto.variaveis.conexoes.emenda
          item.descricao = `Emenda para Perfil Hortivinyl PVC ${this.getPerfil().descricao}mm TEC${this.projeto.ferramenta.descricao}`
          item.ferramenta = this.projeto.ferramenta.descricao

          this.projeto.itens.push(item)
        }
        if (this.projeto.containsCavaletes) {
          const item = {}
          item.quantidade = this.projeto.parametros.quantidadeBancadas * 2
          item.descricao = 'Suporte do Coletor PVC Hortivinyl TEC134S BR 178  274mm'
          this.projeto.itens.push(item)

          let comprimento = this.projeto.parametros.comprimento
          if (comprimento.indexOf(',') > 0) {
            comprimento = parseFloat(comprimento.replace(',', ','))
          }

          const travessa = {
            descricao: 'Travessa Hortivinyl para montagem de bancada TEC134 BR 178 ',
            quantidade: this.projeto.parametros.quantidadeBancadas * this.projeto.variaveis.cavaletes.total,
          }
          debugger
          const larguraEmMili = this.projeto.parametros.largura * 1000
          travessa.descricao = `${travessa.descricao}${larguraEmMili > 999 ? '' : ' '}${larguraEmMili}mm`

          this.projeto.itens.push(travessa)

          const coluna = {
            descricao: 'Perfil Coluna Hortivinyl TEC134C BR 178 1200mm',
            quantidade: travessa.quantidade * 2,
          }

          this.projeto.itens.push(coluna)
        }
      },
      addColetores () {
        const tamanho = this.projeto.variaveis.coletores.tamanho.toLocaleString('pt-BR').replace('.', ',')
        let descricao = `Perfil Coletor Hortivinyl TEC137 Branco  ${tamanho}m C/${this.projeto.parametros.quantidadeLinhas} Furos ${this.projeto.saidaColetor}`

        if (this.projeto.quantidadeLinhas > 23) {
          descricao = `Perfil Coletor Hortivinyl TEC137 Branco ${tamanho}m C/23 Furos ${this.projeto.saidaColetor}`
        }

        this.addItemProjeto(this.projeto.variaveis.coletores.quantidade,
                            descricao,
                            null,
        )
      },
      addInjetores () {
        if (this.projeto.injetor) {
          const injetor = this.projeto.injetores.find((inj) => { return inj.neoId === this.projeto.injetor })

          let descricao
          if (injetor.dimensao === '4x7') {
            descricao = 'Injetor p/Hidroponia 4x7 + conector 3/8"'
          } else if (injetor.dimensao === '7x9') {
            descricao = 'Injetor p/ Hidrop. "3/8" 7x9mm com 30cm'
          }

          const quantidade = this.projeto.parametros.quantidadeBancadas * this.projeto.parametros.quantidadeLinhas
          this.addItemProjeto(quantidade, descricao, null)
        }
      },
      addMesas () {
        if (this.projeto.containsMesas) {
          const mesa = this.projeto.mesasDeGerminacao.find(mesa => { return mesa.neoId === this.projeto.mesa })

          const tamanho = parseInt(mesa.tamanho) * 1000
          let descricao
          if (mesa.descricao.toLowerCase() === 'mesa') {
            descricao = `Mesa de Germinacao ${tamanho}x 618mm Saida - Centro`
          } else {
            descricao = `Tampo de Mesa de Germinacao Branco ${tamanho}x 618mm Saida Central`
          }

          this.addItemProjeto(this.projeto.qtdMesa, descricao, null)
        }
      },
      adicionarPerfis () {
        const barras = this.projeto.barras.split('-')
        const counter = {}
        const linhas = this.projeto.parametros.quantidadeLinhas
        const bancadas = this.projeto.parametros.quantidadeBancadas

        const ferramenta = this.projeto.ferramenta.descricao
        const perfil = `${this.getPerfil().descricao}mm`
        const furo = this.projeto.furos
        const passo = this.projeto.passos

        const qtdTotalBarras = linhas * bancadas

        barras.forEach((barra) => {
          if (counter[barra]) {
            counter[barra] = counter[barra] + 1
          } else {
            counter[barra] = 1
          }
        })

        const tamBarras = Object.keys(counter)
        tamBarras.forEach((key) => {
          let qtd = counter[key] * qtdTotalBarras
          let qtdA = 0
          let qtdB = 0
          const comprimento = parseFloat(key).toFixed(2).replace('.', ',')
          if (qtd % 2 === 0) {
            qtdA = qtd / 2
            qtdB = qtd / 2
          } else {
            if (qtd === 1) {
              qtdA = 1
              qtdB = 0
            } else {
              qtd = qtd - 1
              qtdA = (qtd / 2) + 1
              qtdB = qtd / 2
            }
          }
          if (qtdA > 0) {
            this.projeto.itens.push({
              quantidade: qtdA,
              descricao: `Perfil Hortivinyl PVC ${ferramenta} P&B A ${perfil} X ${comprimento}m Furo${furo}mm Passo${passo}mm`,
            })
          }
          if (qtdB > 0) {
            this.projeto.itens.push({
              quantidade: qtdB,
              descricao: `Perfil Hortivinyl PVC ${ferramenta} P&B B ${perfil} X ${comprimento}m Furo${furo}mm Passo${passo}mm`,
            })
          }
        })
      },
      buildItens () {
        this.projeto.itens = []
        this.adicionarPerfis()
        this.addMesas()
        this.addInjetores()
        this.addColetores()
        this.addTorre()
        this.adicionarTampasPedido()
      },
      getDescription (param) {
        return param.descricao
      },
      validateFields () {
        let errorMessage = 'Para prosseguir por favor verifique os seguintes avisos:'
        let error = false

        // console.log(typeof (this.projeto.perfil))
        // console.log(this.projeto.perfil)
        if (typeof (this.projeto.perfil) !== 'number') {
          errorMessage = `${errorMessage}\nPreencha o campo Perfil`
          error = true
        }

        if (typeof (this.projeto.produto) !== 'number') {
          errorMessage = `${errorMessage}\nPreencha o campo Produto`
          error = true
        }

        if (typeof (this.projeto.injetor) !== 'number') {
          errorMessage = `${errorMessage}\nPreencha o campo Injetor`
          error = true
        }

        if (!this.projeto.parametros.tamanhoDaEstufa) {
          errorMessage = `${errorMessage}\nPreencha o campo Tamanho estufa`
          error = true
        }

        if (!this.projeto.parametros.largura) {
          errorMessage = `${errorMessage}\nPreencha o campo Largura`
          error = true
        }
        if (!this.projeto.parametros.comprimento) {
          errorMessage = `${errorMessage}\nPreencha o campo Comprimento`
          error = true
        }
        if (!this.projeto.barras) {
          errorMessage = `${errorMessage}\nPreencha o campo Barras`
          error = true
        }
        if (!this.projeto.parametros.quantidadeLinhas) {
          errorMessage = `${errorMessage}\nPreencha o campo Quantidade de linhas`
          error = true
        }
        if (!this.projeto.parametros.quantidadeBancadas) {
          errorMessage = `${errorMessage}\nPreencha o campo Quantidade de bancadas`
          error = true
        }
        if (!this.projeto.saidaColetor) {
          errorMessage = `${errorMessage}\nPreencha o campo Saída coletor`
          error = true
        }

        if (this.projeto.barras) {
          const total = parseInt(this.projeto.totalBar)
          const compt = parseInt(this.projeto.parametros.comprimento)

          if (total !== compt) {
            errorMessage = `${errorMessage}\nA soma das barras está diferente do comprimento (Verifique o comprimento total)`
          }
        }

        if (error) {
          alert(errorMessage)
        }

        return error
      },
      viewProjectItens () {
        if (!this.validateFields()) {
          this.showLoader = true
          this.calcularVariáveis()
          this.buildItens()
          this.projeto.formPage = 2
          this.showLoader = false
          // console.clear()
          // console.log(this.projeto.itens)
        }
      },
      addProjeto () {

      },
      printDebug () {
        this.calcularVariáveis()
        this.buildItens()
      },
      deleteProject () {
        const removerOk = confirm('Deseja realmente remover este projeto do orçamento?')

        if (removerOk) {
          // console.log(1)
        }
      },
    },
  }
</script>
